#root {
  width: 100%;
  height: 100%;
}
.App {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: black;
  z-index: 0;
}


.Spin > span {
  font-size: 50px !important;
}

.Spin > span i {
  background-color: rgb(255, 255, 255);
  width: 25px !important;
  height: 25px !important;
}

.Button {
  color: black;
}

.Button:hover, .Button:focus {
  background-color: transparent;
  color: white;
}


@primary-color: #fff;@body-background: #313131;